import {AfterContentInit, Component, Input, OnInit} from '@angular/core';
import { TrackingService } from './tracking.service';
import {animate, style, transition, trigger} from '@angular/animations';
import { ActionTrace } from "ht-maps";
import {debounceTime, filter, map, take, takeUntil} from 'rxjs/operators';
import {IAction} from "ht-models";
import {Observable} from "rxjs/Observable";
import {HtMapService} from "../ht/ht-map.service";
import {TrackingMapService} from "../tracking-map/tracking-map.service";

@Component({
  selector: 'app-tracking',
  template: `<ht-tracking-map [showSummary]="trackingMapService.actionCompleted">
  <ng-container *ngFor="let action of actionsData$ | async; trackBy: trackId">
    <ht-action-summary *ngIf="action.display.show_summary; else status" [action]="action"></ht-action-summary>
    <ng-template #status>
      <ht-action-status [action]="action"></ht-action-status>
    </ng-template>
  </ng-container>

</ht-tracking-map>
<ht-infobox [item]="item" *ngFor="let item of popups$ | async; trackBy: trackId">
  <ht-destination-popup [action]="item.data"></ht-destination-popup>
</ht-infobox>
<ht-infobox [item]="item" *ngFor="let item of userPopup$ | async; trackBy: trackId">
  <ht-user-popup [action]="item.data"></ht-user-popup>
</ht-infobox>
<ht-infobox [item]="item" *ngFor="let item of startPopup$ | async; trackBy: trackId">
  <ht-start-popup [action]="item.data"></ht-start-popup>
</ht-infobox>
<div class="is-overlay" *ngIf="loading" [@fade]>
  <div id="root">
    <div class="loading-container">
      <div class="loading-section">
        <div class="loading-section-ht-logo-pale"></div>
        <div class="loading-section-ht-logo"></div>
      </div>
    </div>
  </div>
</div>
`,
  styles: [`.is-overlay{
  background:#ffffff;
  z-index:1002; }
  .is-overlay #root{
    height:100%;
    width:100%; }
  .is-overlay .loading-container{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-flex:1;
        -ms-flex-positive:1;
            flex-grow:1;
    -webkit-box-orient:vertical;
    -webkit-box-direction:normal;
        -ms-flex-direction:column;
            flex-direction:column;
    height:100%;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;
    -webkit-box-pack:center;
        -ms-flex-pack:center;
            justify-content:center; }
  .is-overlay .loading-section{
    width:74px;
    height:60px;
    margin:0 auto;
    position:relative; }
  .is-overlay .loading-section-ht-logo-pale{
    position:absolute;
    margin:0;
    padding:0;
    height:100%;
    width:100%;
    display:block;
    overflow:hidden;
    background:url("data:image/png;base64,") no-repeat;
    background-size:cover; }

@-webkit-keyframes loading-logo{
  0%{
    width:0%; }
  100%{
    width:100%; } }

@keyframes loading-logo{
  0%{
    width:0%; }
  100%{
    width:100%; } }
  .is-overlay .loading-section-ht-logo{
    position:relative;
    margin:0;
    padding:0;
    height:100%;
    display:block;
    overflow:hidden;
    -webkit-animation:loading-logo 5s linear infinite;
            animation:loading-logo 5s linear infinite;
    background:url("data:image/png;base64,") no-repeat;
    background-size:cover;
    z-index:20; }
`],
  animations: [
    trigger('fade', [
      transition(":leave", [
        animate('300ms ease-in', style({transform: `translateY(100%)`}))
      ])
    ])
  ]
})
export class TrackingComponent implements OnInit, AfterContentInit {
  @Input() shortCode;
  init: boolean = false;
  actionsData$: Observable<IAction[]>;
  popups$;
  userPopup$;
  startPopup$;
  loading: boolean = true;
  // actionSummaryComponent;
  constructor(
    private trackinService: TrackingService,
    public mapService: HtMapService,
    public trackingMapService: TrackingMapService
  ) {
  }

  ngOnInit() {
    this.trackinService.initShortCode(this.shortCode);
    // this.actionSummaryComponent = new ComponentPortal(this.trackinService.actionSummaryComponent);
    // console.log(this.actionSummaryComponent);
    this.actionsData$ = this.trackinService.actions$.asObservable();
    this.init = true;
    this.trackingMapService.setData$(this.actionsData$);
    this.popups$ = this.actionsData$.pipe(
      map((data) => {
        const entities = this.trackingMapService.actionsTrace.destination.entities;
        const keys = Object.keys(entities);
        const mapUtils = this.trackingMapService.actionsTrace.mapInstance.mapUtils;
        return keys.reduce((acc, key) => {
          const entity = entities[key];
          const elem = mapUtils.getElement(entity.item);
          return elem ? [...acc, {data: entity.data, elem, id: key}] : acc
        }, [])
      })
    );

    this.userPopup$ = this.actionsData$.pipe(
      debounceTime(100), //todo fix this null elem on first render
      map((data) => {
        const entities = this.trackingMapService.actionsTrace.user.entities;
        const keys = Object.keys(entities);
        const mapUtils = this.trackingMapService.mapInstance.mapUtils;
        return keys.reduce((acc, key) => {
          const entity = entities[key];
          const elem = mapUtils.getElement(entity.item);
          // console.log("elem", elem);
          const onUpdate = this.trackingMapService.actionsTrace.user.onEntityUpdate(key);
          return elem ? [...acc, {data: entity.data, elem, id: key, onUpdate}] : acc
        }, [])
      })
    );

    const completedAction$ = this.actionsData$.pipe(
      filter((data: IAction[]) => {
        return !!data && data.length && data[0].display.show_summary;
      }),
      take(1)
    );

    this.actionsData$.pipe(
      filter((data: IAction[]) => !!data && data.length && !data[0].display.show_summary),
      takeUntil(completedAction$)
    ).subscribe((action) => {
      this.loading = false;
      this.trackingMapService.resetCleanMap(action)
    });

    completedAction$.subscribe((action) => {
      this.loading = false;
      this.trackingMapService.onComplete(action);
    });

    this.startPopup$ = completedAction$.pipe(
      map((data: IAction[]) => {
        const entities = this.trackingMapService.actionsTrace.start.entities;
        const keys = Object.keys(entities);
        const mapUtils = this.trackingMapService.actionsTrace.mapInstance.mapUtils;
        return keys.reduce((acc, key) => {
          const entity = entities[key];
          const elem = mapUtils.getElement(entity.item);
          return elem ? [...acc, {data: entity.data, elem, id: key}] : acc
        }, [])
      })
    )

  }

  ngAfterContentInit() {


  };

  trackId(a, b) {
    return b.id
  }



}
