import {ChangeDetectionStrategy, Component, Input, OnInit} from '@angular/core';
import {IAction, IPlace} from "ht-models";

@Component({
  selector: 'ht-start-popup',
  template: `<div class="card">
  <div class="card-content is-popup">
    <div class="text-caption">{{action.started_at | timeString}}</div>
    <div class="text-title">{{getStartName(action)}}</div>
  </div>
</div>
`,
  styles: [`.card{
  max-width:150px;
  background:#250D47;
  color:white;
  -webkit-box-shadow:0 2px 6px 0 rgba(114, 114, 114, 0.6);
          box-shadow:0 2px 6px 0 rgba(114, 114, 114, 0.6); }
`],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class StartPopupComponent implements OnInit {
  @Input() action: IAction;
  constructor() { }

  ngOnInit() {

  }

  getStartName(action: IAction): string {
    const place: IPlace = action.started_place;
    if (place) {
      return place.name;
    } else {
      return ""
    }
  }

}
