import {ChangeDetectionStrategy, Component, Input, OnInit} from '@angular/core';
import {IAction, IPlace} from "ht-models";

@Component({
  selector: 'ht-destination-popup',
  template: `<div class="card">
  <div class="card-content is-popup">
    <ng-container *ngIf="action.eta && !action.completed_at; else comp">
      <div class="text-caption">ETA</div>
      <div class="text-title">{{action.eta | timeString }}</div>
    </ng-container>
    <ng-template #comp>
      <div class="text-caption" *ngIf="action.completed_at">{{action.type | nameCase}} | {{action.completed_at | timeString}}</div>
    </ng-template>
    <div class="is-size-7 has-text-weight-bold truncate">{{getDestinationName(action)}}</div>
  </div>
</div>
`,
  styles: [`.card{
  max-width:170px;
  background:#250D47;
  color:white;
  -webkit-box-shadow:0 2px 6px 0 rgba(114, 114, 114, 0.6);
          box-shadow:0 2px 6px 0 rgba(114, 114, 114, 0.6); }

.truncate{
  max-width:170px; }
`],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DestinationPopupComponent implements OnInit {
  @Input() action: IAction;
  constructor() { }

  ngOnInit() {

  }

  getDestinationName(action: IAction): string {
    const place: IPlace = action.completed_place || action.expected_place;
    if (place) {
      return place.name;
    } else {
      return ""
    }
  }

}
