import {ChangeDetectionStrategy, Component, Input, OnInit} from '@angular/core';
import { IAction } from "ht-models";

@Component({
  selector: 'ht-user-popup',
  template: `<div class="card" (click)="openCard()">
  <div class="card-content is-popup">
    <p *ngIf="!opened" class="is-size-7 has-text-weight-bold is-uppercase">
      {{action.user.display.status_text}}
      <span class="icon">
          <i class="ion-chevron-right"></i>
        </span>
    </p>
    <ng-container *ngIf="opened">
      <div class="flex-column column-gap-4">
        <div>
          <div class="is-uppercase is-size-7">{{action.user.display.status_text}} on</div>
          <div class="has-text-weight-bold">Random place</div>
        </div>
        <div>
          <div class="is-uppercase is-size-7">speed</div>
          <div class="has-text-weight-bold">37 kmh</div>
        </div>
        <div class="flex-row row-gap-20">
          <div>
            <div class="is-uppercase is-size-7">distance</div>
            <div class="has-text-weight-bold">{{action.distance | distanceLocale}}</div>
          </div>
          <div>
            <div class="is-uppercase is-size-7">time</div>
            <div class="has-text-weight-bold">{{action.duration | hmString: 60}}</div>
          </div>
        </div>
      </div>
    </ng-container>
  </div>
</div>
`,
  styles: [`.card{
  background-color:#9013FE;
  color:white;
  -webkit-box-shadow:0 2px 6px 0 rgba(114, 114, 114, 0.6);
          box-shadow:0 2px 6px 0 rgba(114, 114, 114, 0.6); }
`],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class UserPopupComponent implements OnInit {
  @Input() action: IAction;
  opened: boolean = false;
  constructor() { }

  ngOnInit() {

  }

  openCard() {
    this.opened = !this.opened;
  }

}
